@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* Import Roboto */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body div.container {
  display: flex;
  overflow-x: hidden;
  max-width: 1050px;
  width: 100%;
  margin: 0 auto;
}

body div.container img#pokeballIcon {
  width: 250px;
  height: 250px;
  position: absolute;
  opacity: 0.1;
  top: -100px;
  left: calc(50% - 125px);
}
body div.container h1.title {
  margin: 15px;
  text-transform: capitalize;
}
div#Index {
  flex: 1 1;
  min-height: 100vh;
  background-color: white;
}
div#Index div#items {
  display: flex;
  width: 96%;
  flex-wrap: wrap;
  margin-left: 2%;
}
div#mainMenu {
  height: 64px;
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
}
div#mainMenu a img {
  width: 20px;
}
a.IndexItem {
  position: relative;
  --margin: 5px;
  box-sizing: border-box;
  width: calc(50%  - 2 * (var(--margin)));
  margin: var(--margin);
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}
a.IndexItem > img {
  position: absolute;
  right: 0;
  width: 40px;
  height: 40px;
  -webkit-filter: invert(1);
          filter: invert(1);
  opacity: 0.6;
}
a.IndexItem > strong {
  text-transform: capitalize;
}
@-webkit-keyframes up-card {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  50% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}
@keyframes up-card {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  50% {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
  }
  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}
div.pokeCard {
  width: 100%;
  background-color: #4ad1b2;
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
div.pokeCard:hover {
  -webkit-animation-name: up-card;
          animation-name: up-card;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
div.pokeCard header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
div.pokeCard header strong {
  margin-bottom: 5px;
  text-transform: capitalize;
}
div.pokeCard header p {
  color: #00000040
}
div.pokeCard .body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
div.pokeCard .body .typesContainer {
  display: flex;
  flex-direction: row;
  grid-gap: 4px;
  gap: 4px;
}
div.pokeCard .typesContainer span.pokeType {
  height: 26px;
  font-size: 13px;
  text-align: center;
  margin-bottom: 5px;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  padding: 5px;
  background-color: #ffffff40;
  border-radius: 5px;
  text-transform: capitalize;
}
div.pokeCard .imageContainer img {
  height: 100px;
  width: auto;
}
div#pokeList {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 5px;
  gap: 5px;
  padding: 6px 16px;
}

@media (min-width: 550px) {
  div#pokeList {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 760px) {
  div#pokeList {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1050px) {
  div#pokeList {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

footer#pokeFooter {
  padding: 0px 16px;
}
footer#pokeFooter button {
  height: 60px;
  outline: none;
  border: 0px;
  margin-bottom: 16px;
  width: 100%;
  background-color: #4ad1b2;
  padding: 15px;
  border-radius: 5px;
  font-size: 12pt;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px #30a086;
}
footer#pokeFooter button:active {
  box-shadow: 0px 0px #30a086;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}
div#Pokedex {
  flex: 1 1;
}
div#Pokemon {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
div#Pokemon h1.title {
  text-transform: capitalize;
  color: white;
}
div#Pokemon div#types span.pokeType {
  margin-left: 10px;
  color: white;
  font-size: 13px;
  text-align: center;
  margin-bottom: 5px;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  padding: 5px 10px;
  background-color: #ffffff40;
  border-radius: 5px;
  text-transform: capitalize;
}
div#Pokemon div#pokemonImage {
  width: 100%;
  display: flex;
  justify-items: center;
  align-items: center;
}
div#Pokemon div#pokemonImage img {
  width: 50%;
  margin: 0 auto;
  z-index: 100;
}
div#Pokemon div#pokemonInfo {
  margin-top: -15px;
  padding: 20px 10px;
  border-radius: 15px 15px 0px 0px;
  width: 100%;
  background-color: white;
  flex: 1 1;
}
div#Pokemon div#pokemonInfo div#tabs {
  display: flex;
  width: 100%;
  justify-content: space-around;
  z-index: 200;
}
div#Pokemon div#pokemonInfo div#tabs button.item {
  background-color: transparent;
  border: 0px;
  outline: none;
  color: gray;
  font-size: 15px;
  text-decoration: none;
}
div#Pokemon div#pokemonInfo div.tabBody {
  margin-top: 10px;
}
div#Pokemon div#pokemonInfo div.tabBody table tr td {
  padding: 3px 10px;
  font-size: 14px;
}
div#Pokemon div#pokemonInfo div.tabBody table tr td:nth-child(1) {
  color: grey;
}
div#Pokemon div#pokemonInfo div.tabBody h4 {
  margin: 10px;
}
div#NotFound {
  text-align: center;
  width: 100%;
}

div#NotFound .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

div#NotFound .App-logo {
  height: 40vmin;
}

div#NotFound header h1 {
  margin: 0px;
}

div#NotFound .App-link {
  color: #09d3ac;
}

